import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import { Provider } from 'react-redux';
import store from './store/store';
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Collection from './taobao/Collection';
import Detail from './goods/Detail';
import Myorder from './taobao/Myorder'
import Search from './taobao/Search'
import Login from './taobao/Login'
const App = ()=>{
	return <Provider store = {store}>
		<Router>
		<Switch>
			<Route exact path='/React05/build' component={Home} />
			<Route path='/collection' component={Collection} />
            <Route path='/order' component={Myorder} />
            <Route path='/mytab' component={Mytab} />
			<Route path='/home/detail/:id' component={Detail} />
			<Route path='/home/search' component={Search} />
			<Route path='/login' component={Login} />
		</Switch>
	</Router>
	</Provider>
}
export default App;