import React from 'react'
import Goodsbottom from './Goodsbottom'
import { Link } from 'react-router-dom';
import store from '../store/store';
const Detail = (props) => {
    let id = props.location.pathname.substring(13,16);
    console.log(id);
    let datalist = store.getState().datalist;
    let src = datalist[id-100].img;
    return (
        <div>
            <img src={src} style={{width:'100%',position:'absolute'}} />
            <Link to='/React05/build'>
                <button
                    style={{
                        width:'30px',
                        height:'30px',
                        background:'rgba(0, 0, 0, 0.4)',
                        color:'#fff',
                        fontSize:'20px',
                        borderRadius:'100%',
                        position:'relative',
                        margin:'15px'
                    }}
                ><span>&lt;</span></button>
            </Link>
                <p style={{position:'relative',marginTop:'330px',
                        color:'#ff5500',fontSize:'25px'}}
                    >￥{datalist[id-100].price}</p>
                <p style={{color:'#000',fontSize:'23px'}}>{datalist[id-100].title}</p>
            <Goodsbottom />
        </div> 
    )
}
export default Detail;