import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import store from '../store/store';
import {BrowserRouter as Router,NavLink} from 'react-router-dom';
//这一页除了返回键是要求，其他跳转我都自己随便设置一个
//支付宝到账一百万元哦亲
class Goodsbootom extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab:'blueTab',
        }
    }
    render() {
        const { pathname } = this.props.location;
        let id = pathname.substring(13,16);
        let datalist = store.getState().datalist;
        let colList = datalist[id-100];
        console.log(colList);
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>   
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="homebusiness firm"
                        icon={
                            <i className='iconfont icon-dianpu'></i>                         
                        }
                        selectedIcon={
                            <i className='iconfont icon-dianpu'></i>                       
                        }
                        onPress={ () => {
                            window.location='http://www.alipay.com'
                        } }
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-lianxikefu'></i>}
                        selectedIcon={<i className='iconfont icon-lianxikefu'></i> }
                        title="联系客服"
                        key="contact customer service"
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1'></i>                        
                        }
                        title="收藏"
                        key="shoucang"
                        selected={this.state.selectedTab === 'onclick'}
                        onPress={() => {
                            this.setState({
                                selectedTab: 'onclick',
                            });
                            // if(!store.getState().username){
                            //     <NavLink to='/login'></NavLink>
                            // }else{
                                const action = {
                                    type:'push_collection',
                                    collection_list:colList
                                }; 
                                store.dispatch(action);
                            //}
                        }
                        
                        }
                        data-seed="logId1"
                    >
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<button style={{height:'22px'}}>加入购物车</button> }
                        key="madd to cart"
                        selected={ pathname === '/tab' }
                        onPress={ () => {
                            window.location='http://www.alipay.com'
                        } }
                    >
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<button style={{height:'22px'}}>立即购买</button> }
                        key="take it now"
                        onPress={ () => {
                            window.location='http://www.alipay.com'
                        } }
                    >
                        
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Goodsbootom);