import React from 'react'
import { SearchBar } from 'antd-mobile';
import {BrowserRouter as Router,NavLink} from 'react-router-dom';
import Mycarousel from './Mycarousel';
import Firstbootom from './Firstbootom';
import store from '../store/store';
import './home.css';
class Home extends React.Component {
    constructor(){
        super();
        this.state = store.getState();
        this.getList = this.getList.bind(this);
        store.subscribe(this.getList)
    }
    componentDidMount = () => {
        fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist')
            .then((res) => res.json())
            .then((myJson) => {
                console.log(myJson);
                let values = myJson.data;
                const action = {
                    type:'get_datalist',
                    list:values
                }
                store.dispatch(action);
            });
    }
    getList(){
        this.setState(store.getState());
    }
    getImage(idx){
        let arr = this.state.datalist;
        for (var i = idx; i < arr.length; i++) {
            let id = store.getState().datalist[i].id;
            //let id1 = store.getState().datalist[i+1].id;
            return (
                <div onClick={() => {this.props.history.push(`/home/detail/${id}`)}} >
                    <img style={{ width: '200px', height: '200px' }} src={arr[i].img} ></img>
                    <span className = 'span1'>￥{arr[i].price}</span>
                    <span className = 'span2'>{arr[i].title}</span>
                </div>     
            )
        }
    }
    render(){
        return (
            <div>
                {/*点击搜索框会跳转页面到搜索页面，也就是说将搜索页面也写成一个组件*/}
                <NavLink to='/home/search'><SearchBar placeholder="寻找宝贝店铺" maxLength={100} /></NavLink>
                {/* 轮播图 */}
                <Mycarousel />
                {/* 商品列表 */}
                <div className='div1'>
                    {this.getImage(0)}
                    {this.getImage(1)}
                    {this.getImage(2)}
                    {this.getImage(3)}
                    {this.getImage(4)}
                    {this.getImage(5)}
                    {this.getImage(6)}
                    {this.getImage(7)}
                    {this.getImage(8)}
                    {this.getImage(9)}
                </div>
                <Firstbootom />
            </div>
        )
    }
}
export default Home;