import React from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Firstbootom extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-faxian-copy'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-faxian-copy'></i>                           
                        }
                        selected={ pathname === '/React05/build' }
                        onPress={ () => {
                            this.props.history.push('/React05/build');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang1'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1'></i>                           
                        }
                        title="收藏"
                        key="cart"
                        selected={ pathname === '/collection' }
                        onPress={ () => {
                            this.props.history.push('/collection')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wodedingdan'></i>
                        }
                        selectedIcon={<i className='iconfont icon-wodedingdan'></i>}                
                        title="我的订单"
                        key="Myorder"
                        selected={ pathname === '/order' }
                        onPress={ () => {
                            this.props.history.push('/order')

                        } }
                    >
                        
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode2'></i> }
                        selectedIcon={ <i className='iconfont icon-wode2'></i> }
                        title="我的淘宝"
                        key="Mytaobao"
                        selected={ pathname === '/mytab' }
                        onPress={ () => {
                            this.props.history.push('/mytab')
                        } }
                    >
                        
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Firstbootom);