import React from "react";
import { SearchBar,WhiteSpace, WingBlank } from 'antd-mobile';
import { NavLink } from "react-router-dom";



//搜索界面进入之后应该是已经拥有光标了
class Search extends React.Component {
    constructor(props){
        super(props);
    }
    componentDidMount() {
        this.autoFocusInst.focus();
    }
    render(){
        return (<div>
            {/* <WingBlank><div>成功进入搜索页面</div></WingBlank> */}
            <SearchBar 
            placeholder="宝贝" 
            ref={ref => this.autoFocusInst = ref} 
            showCancelButton
            // onCancel={this.onBack}
            onCancel={ () => {
                this.props.history.push('/React05/build')
            } }
            />
          </div>);
    }
    // onBack(){
    //     console.log('点击取消按钮触发');
    //     window.location.href = '/';
    // }
}

export default Search;