import React from 'react';
import { Carousel, WingBlank } from 'antd-mobile';
class Mycarousel extends React.Component {
  constructor(props){
    super(props);
    this.state={
      data: ['1', '2', '3','4','5'],
      imgHeight: 176,
    }

  }
  componentDidMount() {
    // 模拟img 加载
    setTimeout(() => {
      this.setState({
        data: ['https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2828936616,3740751483&fm=26&gp=0.jpg', 
        'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1864948861,1675970756&fm=26&gp=0.jpg',
        'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=4029070891,2356943814&fm=26&gp=0.jpg',
        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=3150416535,1238315090&fm=26&gp=0.jpg',
        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=3210004387,1732290172&fm=26&gp=0.jpg'
        ],
      });
    }, 1000);
  }
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay={true}
          infinite = {true}
          //beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          //afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map((item,index) => (
            <a
              key={index}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              {/*点击轮播图的图片进入http://www.alipay.com，支付宝到账一百万元哦亲*/}
              {/* <div>{console.log({index,item})}</div> */}
              <img
                //src={require(`./image/carousel0${index+1}.jpg`)}
                src={`${item}`}
                alt="123"
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // 防火窗调整大小事件以更改高度
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

export default Mycarousel