
const defaultState = {
    datalist:[],
    shoucang:[],
    //userinfo:'false',
    username:'false'
};

export default (state = defaultState,action) => {
    const newState = JSON.parse(JSON.stringify(state));
    switch(action.type){
        case 'get_datalist':         
            newState.datalist = action.list;
            return newState;
        case 'push_collection':
            newState.shoucang.push(action.collection_list);
            return newState;
        case 'delete_collection':
            newState.shoucang.splice(action.idx,1);
            return newState;
        case 'LOGIN':
            // Object.assign({},state,{username:'123'})
            newState.login.assign(action.LOGIN)
            return newState;
        default:
            return state; 
    }
}